<template>
  <v-navigation-drawer
    v-model="drawer"
    :right="false"
    :width="300"
    app
    clipped
  >
    
    <v-list
      class="pt-0 pb-7"
      dense
      expand
      nav
    >
      <template v-for="(item, i) in items">
        <v-subheader
          v-if="item.header"
          :key="`subheader-${i}`"
          v-text="item.header"
        />

        <v-divider
          v-else-if="item.divider"
          :key="`divider-${i}`"
        />

        <base-group
          v-else-if="item.group"
          :key="`group-${i}`"
          :item="item"
        />

        <base-item
          v-else
          :key="`item-${i}`"
          :chip="genChip(item)"
          :icon="item.icon"
          :subtext="item.subtext"
          :text="item.text"
          :to="item.to"
        />

        
      </template>
    </v-list>

    <template v-slot:append>
      <v-divider />
      <div class="px-4 py-2 d-flex">
      <v-spacer />


      </div>
    </template>
  </v-navigation-drawer>
</template>

<script>
  // Utilities
  import kebabCase from 'lodash/kebabCase'
  import { genChip } from '@/util/helpers'
  import { sync } from 'vuex-pathify'

  export default {
    name: 'HomeDrawer',
    components: {
        BaseGroup:() => import('@/components/Group'),
        BaseItem:() => import('@/components/Item'),
    },
    computed: {
      ...sync('app/*'),
      links: sync('drawer/links'),
      children () {
        return this.item.children.map(item => ({
          ...item,
          to: `${this.item.group}/${item.to}`,
        }))
      },
      group () {
        return this.item.children.map(item => {
          return `${this.item.group}/${kebabCase(item.name)}`
        }).join('|')
      },
      items () {
        return this.links
      },
    },

    watch: {
      $route () {
        if (
          this.stateless &&
          this.drawer &&
          this.$vuetify.breakpoint.mdAndDown
        ) this.drawer = false
      },
    },

    methods: {
      genChip,
    },
  }
</script>
